import React, { PureComponent, PropTypes } from 'react';
import classNames from 'classnames/bind';

import '../../styles/global.scss';

import styles from './CreditCard.scss';
const cx = classNames.bind(styles);

// TODO: chrome credit card scan for expire year auto complete
// TODO: chrome auto complete remember style

import flipCard from './animations/flipCard.js';

const inputs_setting = {
  "cardNumber": {
    maxLength: 4,
    nextInputId: "cardNumber2"
  },
  "cardNumber2": {
    maxLength: 4,
    nextInputId: "cardNumber3"
  },
  "cardNumber3": {
    maxLength: 4,
    nextInputId: "cardNumber4"
  },
  "cardNumber4": {
    maxLength: 4,
    nextInputId: "cardExpirationMonth"
  },
  "cardExpirationMonth": {
    maxLength: 2,
    nextInputId: "cardExpirationYear"
  },
  "cardExpirationYear": {
    maxLength: 2,
    nextInputId: "cardHolder",
    cb(cb) {
      cb();
    }
  },
  "cardHolder": {
    maxLength: 60,
    nextInputId: "false"
  },
  "cardCVC": {
    maxLength: 3,
    nextInputId: false,
  }
}

class CreditCard extends PureComponent {
  constructor() {
    super();
    this.state = {
      cardNumber: "",
      cardNumber2: "",
      cardNumber3: "",
      cardNumber4: "",
      cardExpirationMonth: "",
      cardExpirationYear: "",
      cardHolder: "",
      cardCVC: "",
    }
  }
  componentDidMount() {}



  _flipCard = () => {
    flipCard(this.cardContainerNode)
  }


  /**
   * when onChange happens in first card num input
   * because it support for mobile scan
   * so when it get number of length large then four
   * automatic separate it to four inputs
   * 
   * @memberof CreditCard
   */
  _onChangeCardInput = (event) => {
    const inputSetting = inputs_setting[event.target.id];
    const input_val = event.target.value;

    // for mobile camera credit card scan
    if (event.target.id == "cardNumber" && input_val.length > 4 && !this.state[`${event.target.id}__invalid`]) {
      const scan_array = input_val.match(/.{1,4}/g);
      this.setState({
        cardNumber: scan_array[0],
        cardNumber2: scan_array[1],
        cardNumber3: scan_array[2],
        cardNumber4: scan_array[3],
      })
    } else if (input_val.length <= inputSetting.maxLength) { //
      this.setState({
        [event.target.id]: input_val
      });
    }

    // input done
    if (input_val.length === inputSetting.maxLength) {
      // valid check
      const reg_input = new RegExp(event.target.pattern)
      const input_invalid = !reg_input.test(input_val);

      // focus to next input field, and try to call callback from setting 
      if (!input_invalid) {
        const next_input = document.getElementById(inputSetting.nextInputId);
        next_input && next_input.focus();

        // input valid cb
        inputSetting.cb && inputSetting.cb(this._flipCard)
        this.setState({
          [`${event.target.id}__invalid`]: input_invalid
        }, () => {
          this._checkAllInputValid();
        })
      } else {
        this.setState({
          [`${event.target.id}__invalid`]: input_invalid
        })
      }
    }
  }

  _checkAllInputValid = () => {
    const allInputValid = Object.keys(inputs_setting).reduce((acc, id) => {
      console.log(`${id}__invalid`);
      console.log(this.state[`${id}__invalid`]);
      return acc && this.state[`${id}__invalid`] === false;
    }, true)
    console.log(allInputValid);
    this.setState({
      all_valid: allInputValid
    })
  }

  onSubmit = (event) => {
    event.preventDefault();
  }

  render() {
    const {all_valid} = this.state;
    return (
      <div className={ styles.container }>
        <form
          ref={ form => this.cardContainerNode = form }
          onSubmit={ this.onSubmit }
          className={ styles.card + ` active` }>
          <div className={ cx("card__front", "card") }>
            <i
              onClick={ this._flipCard }
              className={ styles.btn__turnOver + " fa fa-repeat" }
              aria-hidden="true"></i>
            <h1>PAYMENT DETAILS</h1>
            <div className={ styles.front__inputs }>
              <label className={ styles.label__cardNum } htmlFor="cardNumber__invalid">
                <h3>CARD NUMBER</h3>
                <input
                  onChange={ this._onChangeCardInput }
                  id="cardNumber"
                  name="cardnumber"
                  className={ cx("input__style", "input__cardNum", {
                                "input__invalid": this.state['cardNumber__invalid']
                              }) }
                  type="text"
                  autoComplete="cc-number"
                  value={ this.state.cardNumber }
                  pattern="[0-9]{4}"
                  size="16"
                  maxLength="16"
                  required/>
                <input
                  id="cardNumber2"
                  className={ cx("input__style", "input__cardNum", {
                                "input__invalid": this.state['cardNumber2__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardNumber2 }
                  type="text"
                  pattern="[0-9]{4}"
                  size="4"
                  maxLength="4"
                  required/>
                <input
                  id="cardNumber3"
                  className={ cx("input__style", "input__cardNum", {
                                "input__invalid": this.state['cardNumber3__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardNumber3 }
                  type="text"
                  pattern="[0-9]{4}"
                  title="Should be four number"
                  size="4"
                  maxLength="4"
                  required/>
                <input
                  id="cardNumber4"
                  className={ cx("input__style", "input__cardNum", {
                                "input__invalid": this.state['cardNumber4__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardNumber4 }
                  type="text"
                  pattern="[0-9]{4}"
                  size="4"
                  maxLength="4"
                  required/>
              </label>
              <label className={ styles.label__expiration } htmlFor="cardExpirationMonth">
                <h3>EXPIRATION DATE</h3>
                <input
                  id="cardExpirationMonth"
                  name="expirationMonth"
                  className={ cx("input__style", {
                                "input__invalid": this.state['cardExpirationMonth__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardExpirationMonth }
                  type="text"
                  pattern="[0-9]{2}"
                  placeholder="MM"
                  autoComplete="cc-exp-month"
                  size="2"
                  maxLength="2"
                  required/>
                <input
                  id="cardExpirationYear"
                  name="expirationYear"
                  className={ cx("input__style", {
                                "input__invalid": this.state['cardExpirationYear__invalid']
                              }) }
                  onChange={ this._onChangeCardInput }
                  value={ this.state.cardExpirationYear }
                  type="text"
                  pattern="[0-9]{4}"
                  placeholder="YYYY"
                  autoComplete="cc-exp-year"
                  size="4"
                  maxLength="4"
                  required/>
              </label>
            </div>
          </div>
          <div className={ cx("card__back", "card") }>
            <i
              onClick={ this._flipCard }
              className={ styles.btn__turnOver + " fa fa-repeat" }
              aria-hidden="true"></i>
            <label className={ styles.label__cardHolder } htmlFor="cardHolder">
              <h3>CARD HOLDER</h3>
              <input
                id="cardHolder"
                name="ccname"
                type="text"
                onChange={ this._onChangeCardInput }
                value={ this.state.cardHolder }
                pattern="[\w\s]{3,}"
                autoComplete="cc-name"
                className={ cx("input__style", "input__cardHolder", {
                              "input__invalid": this.state['cardHolder__invalid']
                            }) }
                required></input>
            </label>
            <label className={ styles.label__cardCVC } htmlFor="cardCVC">
              <h3>CVC</h3>
              <input
                id="cardCVC"
                name="cvc"
                type="number"
                pattern="[0-9]{3,}"
                onChange={ this._onChangeCardInput }
                value={ this.state.cardCVC }
                maxLength="4"
                minLength="3"
                size="3"
                className={ cx("input__style", "input__cardCVC", {
                              "input__invalid": this.state['cardCVC__invalid']
                            }) }
                required></input>
            </label>
          </div>
        </form>
        <button type="submit" className={ cx("btn__submit", {
                                            "btn__submit--active": all_valid
                                          }) }>
          Pay $500
        </button>
      </div>
      );
  }
}

CreditCard.propTypes = {

};

export default CreditCard;



